<template>
<div class="head">
  <div class="logo">惠答</div>
  <i class="el-icon-d-arrow-right" @click="isShowNav"></i>
  <div class="user">欢迎你，西伯利亚狼</div>
</div>
</template>

<script>
import Bus from '../../utils/bus.js'
export default {
  name: 'Head',
  methods: {
    isShowNav () {
      Bus.$emit('isShowNav')
    }
  }
}
</script>

<style scoped>
.head{
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.logo{
  width: 90px;
  padding: 8px 0;
  text-align: center;
  color: #fff;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 30px;
}
.el-icon-d-arrow-right{
  position: absolute;
  bottom: -10px;
  left: 170px;
}
.user{
  color: rgb(228, 225, 225);
  font-size: 18px;
}
</style>



