import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import utils from '@/utils/tools'

import store from './vuex'

Vue.use(ElementUI)
Vue.use(utils)
Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  // render: h => h(App)
  components: {
    App
  },
  template: '<App/>'
})



