<template>
  <div class="container">
    <div id="particles-js"></div>
    <div class="user_form">
      <el-form :model="userFormData"
               status-icon
               ref="userForm"
               label-width="100px"
               class="demo-ruleForm">
        <el-form-item label="用户名"
                      prop="username"
                      :rules="[
                        { required: true, message: '年龄不能为空'}
                      ]">
          <el-input type="username"
                    v-model="userFormData.username"></el-input>
        </el-form-item>
        <el-form-item label="密码"
                      prop="password"
                      :rules="[
                        { required: true, message: '年龄不能为空'}
                      ]">
          <el-input type="password"
                    v-model="userFormData.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary"
                     @click="submitForm()">提交</el-button>
          <el-button @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginIndex',
  data () {
    return {
      userFormData: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    submitForm () {
      this.$refs['userForm'].validate((valid, params) => {
        if (valid) {
          /* this.$http.jsonp('/api/jsonp', this.userFormData).then(res => {
            console.log(res)
          }) */
          this.$router.push('/home')
        } else {
          console.log('error submit!!');
          console.log(params);
          return false;
        }
      });
    },
    resetForm () {
      this.$refs['userForm'].resetFields();
    }
  },
  mounted () {
    particlesJS.load('particles-js', '/static/data/particles.json', function () {
      console.log('callback - particles.js config loaded');
    });
  }
}
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
  background: url('../../assets/images/stellar.jpg') no-repeat;
  background-size: 100% 100%;
}
.user_form {
  width: 400px;
  height: 170px;
  background: rgba(160, 157, 157, 0.6);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 10px;
  padding: 15px 15px 15px 0;
}
</style>



