export default {
  namespaced: 'test', // 命名空间，以区别模块和全局的state/action/mutation
  state: {
    count: 1
  },
  actions: {
    add ({commit}, payLoad) {
      commit('ADD', payLoad)
    },
    reduce (context, payLoad) {
      context.commit('REDUCE', payLoad)
    }
  },
  mutations: {
    ADD (state, payLoad) {
      state.count += payLoad
    },
    REDUCE (state, payLoad) {
      state.count -= payLoad
    }
  }
}



