import Http from './fetcher';
export default {
  install (Vue) {
    Object.defineProperty(Vue.prototype, '$http', {
      writable: false, // 可写
      enumerable: false, // 可枚举
      configurable: false, // 可配置
      value: Http
    });
  }
};



