export default {
  namespaced: true,
  state: {
    count: '这里是teacher模块里面的count变量'
  },
  actions: {
    setCount ({commit}) {
      commit('SETCOUNT')
    }
  },
  mutations: {
    SETCOUNT (state) {
      state.count = '模块teacher的count值改变'
    }
  }
}



