import { setPageTitle } from '../../mutation_type'
export default {
  namespaced: 'home', // 命名空间，以区别模块和全局的state/action/mutation
  state: {
    pageTitle: '' // 页面的title值
  },
  actions: {
    setPageTitle ({commit}, payLoad) {
      commit('SETPAGETITLE', payLoad)
    }
  },
  mutations: {
    [setPageTitle] (state, payLoad) {
      state.pageTitle = payLoad
    }
  }
}



