<template>
<el-aside width="auto">
  <el-menu
    :default-active="$route.path"
    class="el-menu-vertical-demo"
    @open="handleOpen" @close="handleClose"
    :collapse="isCollapse"
    background-color="#F9FAFB"
    text-color="#999"
    active-text-color="#333"
    router
  >
    <el-submenu
      v-for="(item, index) in list"
      :key="index"
      :index="'1_' + index"
    >
      <template slot="title">
        <i :class="item.iconType"></i>
        <span slot="title">{{item.menuName}}</span>
      </template>
      <el-menu-item
        v-for="(item1, index1) in item.childMenu"
        :key="index1"
        :index="'/home/' + item1.route.path"
        :route="item1.route"
      >{{item1.menuName}}</el-menu-item>
    </el-submenu>
  </el-menu>
</el-aside>
</template>

<script>
import Bus from '../../utils/bus.js'
import mockData from '../../../static/data/routes.js'
export default {
  name: 'Aside',
  data () {
    return {
      isCollapse: false,
      list: mockData
    };
  },
  mounted () {
    console.log(this.$route.path)
    Bus.$on('isShowNav', () => {
      this.isCollapse = !this.isCollapse
    })
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style scoped>
.el-aside {
  background-color: #F9FAFB;
  color: #333;
  text-align: center;
  line-height: 200px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 100%;
}
</style>



