import {conGetData} from '@/api'

export default {
  namespaced: 'interactionContent',
  state: {
    tableData: '',
    loading: false,
    total: 10,
    options: {
      pageSize: 5,
      pageNum: 1,
      tid: 1
    }
  },
  actions: {
    getTableData ({commit, state}) {
      commit('SETLOADING')
      conGetData('/api/getTableData', state.options).then(res => {
        if (res.code === 1) {
          commit('SETTOTAL', res.total)
          commit('SETTABLEDATA', res.data)
          commit('SETLOADING')
        }
      })
    },
    setOptions ({commit}, payLoad) {
      commit('SETOPTIONS', payLoad)
    }
  },
  mutations: {
    SETTOTAL (state, payLoad) {
      state.total = payLoad
    },
    SETOPTIONS (state, payLoad) {
      state.options = Object.assign({}, state.options, payLoad)
    },
    SETTABLEDATA (state, payLoad) {
      state.tableData = payLoad
    },
    SETLOADING (state) {
      state.loading = !state.loading
    }
  }
}



