import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
import Home from '@/components/home'

// 二级路由
const TeacherList = (resolve) => require(['@/components/teacher/teacherList'], resolve)
const CheckPending = (resolve) => require(['@/components/teacher/checkPending'], resolve)
const Withdraw = (resolve) => require(['@/components/teacher/withdraw'], resolve)
const Activity = (resolve) => require(['@/components/interaction/activity'], resolve)
const Content = (resolve) => require(['@/components/interaction/content'], resolve)
const TypeInQuestion = (resolve) => require(['@/components/interaction/typeInQuestion'], resolve)

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        title: '登录页'
      }
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      redirect: '/home/welcome',
      children: [
        {
          path: 'welcome', // 欢迎页面
          name: 'welcome',
          component: {
            template: '<div>欢迎页面</div>'
          },
          meta: {
            title: '欢迎页面'
          }
        },
        // teacher
        {
          path: 'checkPending', // 待审核列表
          name: 'CheckPending',
          component: CheckPending,
          meta: {
            title: '待审核列表'
          }
        },
        {
          path: 'teacherList', // 导师列表
          name: 'TeacherList',
          component: TeacherList,
          meta: {
            title: '导师列表'
          }
        },
        {
          path: 'withdraw', // 提现管理
          name: 'Withdraw',
          component: Withdraw,
          meta: {
            title: '提现管理'
          }
        },
        // interaction
        {
          path: 'activity', // 活动管理
          name: 'Activity',
          component: Activity,
          meta: {
            title: '活动管理'
          }
        },
        {
          path: 'conManage', // 内容管理
          name: 'Content',
          component: Content,
          meta: {
            title: '内容管理'
          }
        },
        {
          path: 'typeInQuestion', // 录入问答
          name: 'ConTypeInQuestiontent',
          component: TypeInQuestion,
          meta: {
            title: '内容管理-录入问答'
          }
        }
      ]
    }
  ]
})
router.beforeEach((to, from, next) => {
  const title = to.meta.title
  document.title = title
  setTimeout(() => {
    router.app.$store.dispatch('home/setPageTitle', title)
  }, 0)
  next()
})
export default router



