<template>
<div class="container">
  <el-container>
    <el-header>
      <header-com/>
    </el-header>
    <el-container>
      <aside-com/>
      <el-main>
        <router-view>
          <div class="routerType" slot="pageTitle">
            <span>{{pageTitle}}</span>
          </div>
        </router-view>
      </el-main>
    </el-container>
  </el-container>
</div>
</template>

<script>
import Head from './header'
import Aside from './aside'
export default {
  name: 'Home',
  components: {
    'header-com': Head,
    'aside-com': Aside
  },
  computed: {
    pageTitle () {
      return this.$store.state.home.pageTitle
    }
  }
}
</script>

<style scoped>
.container,.el-container{
  height: 100%;
}
.el-header {
  background-color: #3A76E5;
  color: #fff;
  height: auto;
  padding: 10px 20px;
}
.el-main {
  background-color: #fff;
}
</style>



