import 'whatwg-fetch';// 兼容低版本浏览器的fetch请求
import fetchJSONP from 'fetch-jsonp'; // 标准Fetch API不支持JSONP。fetch-jsonp提供相同的API来获取JSONP，就像本机Fetch一样，也带有全局fetchJsonp函数。
// 定义公共的req
const reqHeaders = {
  Accept: 'text/json',
  token: '1'
}

// json对象转字符串url
const obj2url = obj => {
  let arr = [];
  Object.keys(obj).forEach(key => {
    arr.push(`${key}=${obj[key]}`);
  });
  return arr.join('&');
};

// 抽离公共请求逻辑
const request = (url, options) =>
  new Promise((resolve, reject) => {
    return fetch(url, {
      ...options
    })
      .then(res => {
        if (res.ok) {
          resolve(res.json())
        } else {
          reject(res)
        }
      })
  });

export default {
  get (url, params) {
    let url1 = params ? `${url}?${obj2url(params)}` : `${url}`
    return request(url1, {
      method: 'GET',
      headers: reqHeaders
    });
  },
  post (url, params) {
    let body = params ? obj2url(params) : ''
    return request(url, {
      method: 'POST',
      headers: Object.assign({}, reqHeaders, {
        'content-type': 'application/x-www-form-urlencoded'
      }),
      body
    });
  },
  // jsonp跨域请求
  jsonp (url, params) {
    return fetchJSONP(`${url}?${obj2url(params)}`, {
      jsonpCallback: 'custom_callback'
    }).then(res => res.json())
  }
};



